.searchIcon {
  top: 0;
  left: 0;
  width: 28px;
  position: absolute;
  margin: 13px 0 0 18px;
  opacity: 0.6;
}

.noDisplay {
  display: none;
  opacity: 0;
  visibility: hidden;
}

.close-icon {
  position: absolute;
	padding-top: 50px;
	border:1px solid transparent;
	background-color: transparent;
	display: inline-block;
	vertical-align: middle;
  outline: 0;
  cursor: pointer;
}

.close-icon:after {
	content: "X";
	display: block;
	width: 12px;
	height: 12px;
	position: absolute;
	background-color: rgb(186, 216, 192);
	z-index:1;
	right: 32px;
	top: 0;
	bottom: 0;
	margin: auto;
	padding: 4px;
	border-radius: 50%;
	text-align: center;
	color: white;
	font-weight: normal;
	font-size: 13px;
	cursor: pointer;
}

.searchBar:not(:valid) ~ .close-icon {
	display: none;
}

